.social {
  display: flex;
  justify-content: space-between;
  width: 141px;
  margin: 0 auto;
  padding: 0;

  @media (min-width: $tablet) {
    margin: 8px 0 0 70px;
  }
}

.social__item {
  list-style-type: none;
}

.social__item svg:hover {
  fill: #46c1ae;
}

.social__item svg:active {
  fill: #46c1ae;
  opacity: 0.3;
}
